"use strict";

document.getElementById('open').addEventListener('click', function () {
  var headerNav = document.querySelector('.header__nav');
  var thePage = document.querySelector('.the-page');
  headerNav.classList.add('moveIn');
  thePage.classList.add('move-left');
});
document.getElementById('close').addEventListener('click', function () {
  var headerNav = document.querySelector('.header__nav');
  var thePage = document.querySelector('.the-page');
  headerNav.classList.remove('moveIn');
  thePage.classList.remove('move-left');
}); // Testimonial Slider
"use strict";

document.getElementById('hide').addEventListener('click', function () {
  var note = document.querySelector('.note');

  var slideUp = function slideUp(target) {
    target.style.transform = 'translateY(-100%)';
    target.style.height = 0;
    target.style.paddingTop = 0;
    target.style.paddingBottom = 0;
    target.style.marginTop = 0;
    target.style.marginBottom = 0;
    target.style.overflow = 'hidden';
  };

  slideUp(note); // slideUp(note, 500);
});
"use strict";

var testimonialItems = document.querySelector('.testimonial__items');
var testimonialItem = Array.from(testimonialItems.children);
var sliderBtns = document.querySelector('.testimonial__btns');
var sliderBtn = Array.from(sliderBtns.children);
var itemWidth = testimonialItem[0].getBoundingClientRect().width;
testimonialItem.forEach(function (item, i) {
  item.style.left = "".concat(itemWidth * i, "px");
});
sliderBtns.addEventListener('click', function (e) {
  var targeSlideBtn = e.target.closest('button');
  if (!targeSlideBtn) return;
  var activeSlide = testimonialItems.querySelector('.active');
  var activeSlideBtn = sliderBtns.querySelector('.active');
  var targetBtnIndex = sliderBtn.findIndex(function (dot) {
    return dot === targeSlideBtn;
  });
  var targetSlide = testimonialItem[targetBtnIndex];
  testimonialItems.style.transform = "translateX(-".concat(targetSlide.style.left, ")");
  console.log(targetSlide.style.left);
  activeSlide.classList.remove('active');
  targetSlide.classList.add('active'); // update slider Dot

  activeSlideBtn.classList.remove('active');
  targeSlideBtn.classList.add('active');
});